"use strict";

document.addEventListener('DOMContentLoaded', function (e) {
  var testimonialForm = document.getElementById('leoadd-testimonial-form');
  testimonialForm.addEventListener('submit', function (e) {
    e.preventDefault();
    console.log('Prevent form to submit 111');

    // reset the form messages
    resetMessages();

    // collect all the data
    var data = {
      'name': testimonialForm.querySelector('[name="name"]').value,
      'email': testimonialForm.querySelector('[name="email"]').value,
      'message': testimonialForm.querySelector('[name="message"]').value
    };

    // validate the email
    if (!validateEmail(data.email)) {
      testimonialForm.querySelector('[data-error="invalidEmail"]').classList.add('show');
      return;
    }

    // ajax http post request
    var url = testimonialForm.dataset.url;
    console.log(url);
  });
});
function resetMessages() {
  document.querySelectorAll('.field-msg').forEach(function (f) {
    return f.classList.remove('show');
  });
}
function validateEmail(email) {
  var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
}

